@keyframes fondo-anim{
  0%{background-position: left;} /*por defecto*/
  100%{background-position: right;}
}

.boton-rojo{
  background-color: red;
}

.contador-tareas{
  text-align: center;
  margin-bottom: 3%;
  font-size: 1.3rem;
}